export default {
    layout: "Emp-content",
    data() {
        return {
            office_time: {
                type: "",
                office_time: "",

            },
            errors: [],
        };
    },
    methods: {
        addoffice_time() {

            this.$axios
                .$post("/office_time/add", this.office_time)
                .then((res) => {
                    this.office_time = "";
                    this.errors = "";
                    this.$toaster.success(res.message);
                    this.$router.push("/employee/office_time");
                })
                .catch((error) => {
                    this.errors = error.response.data.errors;
                    console.log(this.errors);
                });
        },
    },

};
